@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.geral{
  display: flex;
  flex-direction: column;
  background-color: #ebf2ff;
}

.modalp{
  width:400px;
  height: 500px;
}

.divModal{
  background: #7386D5;
  position: fixed;
  width: 400px;
  height:250px;
  margin-top: 200px;
  margin-left: 700px;
  border-radius: 15px;
  box-shadow:10px 10px 24px -5px rgba(0,0,0,0.75);
  z-index: 5;
}

@media (max-width: 1500px) {
  .divModal {
      margin-left: 450px;
  }
}

@media (max-width: 1200px) {
  .divModal {
      margin-left: 350px;
  }
}

@media (max-width: 900px) {
  .divModal {
      margin-left:200px;
  }
}

.divModal .modalHeader h1{
  font-size: 11px;
  text-align: center;
  border-radius: 15px;
  padding: 10px;
  color: #FFFFFF;
}

.divModal .modalHeader{
  border-radius: 15px;
}

.modalContent{
  margin-left: 15px;
  width: 370px;
  height:150px;
  background-color: #FFFFFF;
  border:none;
  border-radius: 10px;
  outline: none;
}


.modalContent input{
  margin-top: 20px;
  margin-left: 20px;
  width:80%;
  height: 70%;
  text-align: top;
  border-radius: 10px;
  border:none;
  outline: none;

}


.modalActions .btnModal{
  width: 80px;
  height: 35px;
  background-color: #7386D5;
  font-size: 10px;
  color: #FFFFFF;
  border:none;
  transition: 1s;
  margin-top: 10px;
  margin-left: 75px;
  border-radius: 10px;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
}

.modalActions .btnModal:hover{

  background-color: #FFFFFF;
  color: #7386D5;

}

.employeeHeaderWithoutValue{
  margin-top: -350px;
  display: inline-block;
  border-radius: 15px;
  width: 100%;
  height: 120px;
  background-color: #f1526d;
  box-shadow: 10px 10px 20px -6px rgba(0,0,0,0.45);
}

.employeeHeaderWithValue{
  margin-top: -350px;
  display: inline-block;
  border-radius: 15px;
  width: 100%;
  height: 120px;
  background-color: #5cdbc0;
  box-shadow: 10px 10px 20px -6px rgba(0,0,0,0.45);
}

.icon+{
  font-size: 40px;
  text-align: center;
  vertical-align: middle;
}

.icon-{
  font-size: 40px;
  text-align: center;
  vertical-align: middle;
}

.input{
  list-style: none;
}

.input:hover{
  transition: none;
}

.buttonCreateEventButton{
  z-index: 4;
  position: fixed;
  margin-top: 100px;
}
.employeeWrapper{
    background-color: #91a2eb;
    border-radius: 15px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    transition-duration: 1s;
    box-shadow: 10px 10px 31px -6px rgba(0,0,0,0.45);

}

.employeeName{
    color: #FFFFFF;
    font-family: Helvetica;
}

.employeeInfo{
    color: #FFFFFF;
    font-family: Helvetica;
}

.additionalInfo{
    color: #FFFFFF;
    font-family: Helvetica;
    font-size: 15px;
}

.maxValue{

    text-align: right;
    color: #FFFFFF;
    font-size: 12px;
}




.progress-bar:only-child{

    text-align: right;
    
}

.progressWidth{
    color: #FFFFFF;
    text-align: right;
}

.createEventButton{
    background-color: #91a2eb;
    color: #FFFFFF;
    border: none;
    outline: none;
}

.wrapper {
    display: flex;
    align-items: stretch;
    z-index: 4;
    text-align: left;
}

#sidebar {
    min-width: 250px;
    max-width: 250px;
    min-height: 100vh;
    position:fixed;
    box-shadow:6px 0px 18px -9px rgba(0,0,0,0.75)
}
#sidebar.active {
    margin-left: -250px;
}
a[data-toggle="collapse"] {
    position: relative;
    text-decoration: none;
}

a:hover{
    text-decoration: none;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

@media (max-width: 1500px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
}


body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

#sidebar {
    background: #7386D5;
    color: #fff;
    transition: all 0.3s;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #6d7fcc;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #47748b;
}

ul{
    list-style-type: none;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

a:only-child{
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
    

}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}
#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active > a, a[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}
ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
}

.buttonList{
    width: 150px;
    
    margin-top: 20px;
    margin-left: 50px;
}
